      -ms-transform: translateX(10px);
          transform: translateX(10px);
}

.Settings .Results .Result-player .Photo {
  height: 56px;
  margin: 0 10px;
  display: inline-block;
  vertical-align: top;
}

.Settings .Result-player .Icon {
  height: 40px;
  margin-top: 8px;
  display: inline-block;
  vertical-align: top;
  position: absolute;
}

.Settings .Result-player .Icon {
