}

.Settings .Menu {
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 162px;
  line-height: 56px;
  height: 56px;
  display: inline-block;
  background: #fafafa;
  border-radius: 3px;
  padding: 0 20px;
  border: none;
  color: #000;
  cursor: pointer;
  text-align: center;
  -webkit-transform-origin: bottom left;
      -ms-transform-origin: bottom left;
          transform-origin: bottom left;
  -webkit-transition: 0.3s -webkit-transform ease-in-out;
  transition: 0.3s -webkit-transform ease-in-out;
  -o-transition: 0.3s transform ease-in-out;
  transition: 0.3s transform ease-in-out;
  transition: 0.3s transform ease-in-out, 0.3s -webkit-transform ease-in-out;
  will-change: transform;
  -webkit-tap-highlight-color: rgba(104, 27, 39, 0.1);
}

.Settings .Menu .Selected {
  margin: 0;
  -webkit-transition: 0.3s -webkit-transform ease-in-out;
  transition: 0.3s -webkit-transform ease-in-out;
  -o-transition: 0.3s transform ease-in-out;
  transition: 0.3s transform ease-in-out;
  transition: 0.3s transform ease-in-out, 0.3s -webkit-transform ease-in-out;
}

.Settings .Menu.expanded {
  border-radius: 0 0 3px 3px;
}

.Settings .Menu.expanded .Selected {
  margin: 0;
}

.Settings .Menu .Options {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
  -webkit-transform-origin: bottom center;
      -ms-transform-origin: bottom center;
          transform-origin: bottom center;
  width: 162px;
  border-radius: 3px 3px 0 0;  
  position: absolute;
  left: 0;
  background: #fafafa;
  bottom: 56px;
  overflow: hidden;
  margin: 0;
  -webkit-transition: 0.3s -webkit-transform ease-in-out;
  transition: 0.3s -webkit-transform ease-in-out;
  -o-transition: 0.3s transform ease-in-out;
  transition: 0.3s transform ease-in-out;
  transition: 0.3s transform ease-in-out, 0.3s -webkit-transform ease-in-out;
  will-change: transform;
  -webkit-transform: scaleY(0);
      -ms-transform: scaleY(0);
          transform: scaleY(0);
  /* box-shadow: 0 3px 6px 1px rgba(0, 0, 0, 0.06); */
}

.Settings .Menu.expanded .Options {
  -webkit-transform: scaleY(1);
      -ms-transform: scaleY(1);
          transform: scaleY(1);
}

.Settings .Menu .Options div:not(:last-child) {
  background: #fafafa;
  height: 56px;
  width: 162px;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}

.Settings .Menu .Options div:first-child {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.Settings .Menu .Options div:hover {
