  width: 540px;
  height: 540px;
  background: #2E7D32;
  -webkit-box-shadow: 0 3px 6px 1px rgba(0, 0, 0, 0.06);
          box-shadow: 0 3px 6px 1px rgba(0, 0, 0, 0.06);
  -webkit-perspective: 800px;
          perspective: 800px;
  overflow: hidden;
}

.Pitch > div {
  height: 100%;
  background: -webkit-gradient(linear, left top, left bottom, color-stop(5%, rgba(255, 255, 255, 0.2)), to(rgba(255, 255, 255, 0)));
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0.2) 5%, rgba(255, 255, 255, 0) 100%);
  background: -o-linear-gradient(top, rgba(255, 255, 255, 0.2) 5%, rgba(255, 255, 255, 0) 100%);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.2) 5%, rgba(255, 255, 255, 0) 100%);
}

.Pitch .Outlines {
  position: absolute;
  display: block;
  -webkit-transform-origin: bottom center;
      -ms-transform-origin: bottom center;
          transform-origin: bottom center;
  -webkit-transform: scale(1.41);
      -ms-transform: scale(1.41);
          transform: scale(1.41);
  pointer-events: none;
  bottom: -13%;
  left: 5%;
  width: 90%;
  height: 76%;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Pitch .Signature {
  background: rgba(255, 255, 255, 0.2);
  color: rgba(0, 0, 0, 0.95);
  text-align: center;
  font-size: 12px;
  width: 20%;
  line-height: 140%;
  display: block;
  margin: 0 auto;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Trash {
  position: absolute;
  top: 5%;
  width: 70%;
  margin: 0 15%;
  background: #681b27;
  border-radius: 3px;
  z-index: 300;
  line-height: 50px;
  text-align: center;
  font-size: 16px;
  display: none;
  -webkit-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

.Trash.visible {
  display: block;
}

.Trash.active {
  background: #000;
}

.Pitch .LineupName {
  font-family: 'saira condensed', Arial, Helvetica, sans-serif;
  font-size: 36px;
  z-index: 100;
  width: 70%;
  font-weight: 800;
  top: 5%;
  text-align: center;
  position: absolute;
  text-transform: uppercase;
  margin: 0 15%;
  line-height: 50px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Pitch .EditLineupName {
  font-family: 'saira condensed', Arial, Helvetica, sans-serif;
  font-size: 36px;
  font-weight: 800;
