}

.Pitch .PositionIndicator {
  position: absolute;
  width: 6%;
  height: 6%;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  -webkit-transform: translate(-50%) rotateX(45deg);
          transform: translate(-50%) rotateX(45deg);
}

